﻿@import "../../base/fn";

.weui_btn_warn {
    background-color: $weuiBtnWarnBg;
    &:not(.weui_btn_disabled):visited {
        color: $weuiBtnFontColor;
    }
    &:not(.weui_btn_disabled):active {
        color: $weuiBtnActiveFontColor;
        background-color: $weuiBtnWarnActiveBg;
    }
}
